import React, { useEffect, useState, useCallback } from 'react';
import ReactDOM from 'react-dom';
import { Button, Progress, Spinner } from 'reactstrap';
import useVisibilityHandlers from '../hooks/useVisibilityHandlers';
import { Card } from '../simpleui';
import { fetchNFLDataAsync } from '../SportsDataAccessors/NFLHelper';
import { Schedule } from '../SportsDataAccessors/types';
import GameTable from './GameTable';

interface NflUserDataI {
  removedGames: {
    date: string;
    games: { // wanted to use Set but its a pain to stringify and store
      [key: number]: boolean;
    }
  }
}

export default function NFL() {
  const LOCAL_STORAGE_KEY = 'nfl-schedule-data';
  const USER_DATA_LOCAL_STORAGE_KEY = 'nfl-user-data';

  const [userLocalData, setUserLocalData] = useState<NflUserDataI>(() => {
    const savedUserDataString = localStorage.getItem(USER_DATA_LOCAL_STORAGE_KEY);
    if (savedUserDataString) {
      const savedUserDataJSON = JSON.parse(savedUserDataString);
      return savedUserDataJSON;
    }

    return {
      removedGames: {
        date: '',
        games: {}
      }
    }
  });

  const [resetCounter, setResetCounter] = useState<number>(0);

  const [schedule, setSchedule] = useState<Schedule>(() => {
    console.log('using cache')

    const cachedScheduleString = localStorage.getItem(LOCAL_STORAGE_KEY);
    if (cachedScheduleString) return JSON.parse(cachedScheduleString);
    else return { displayDate: '', games: [] };
  });

  const [isLoading, setIsLoading] = useState<boolean>(false);

  const upDateSchedule = async () => {
    console.log('<updating>');
    console.log(schedule);
    setIsLoading(true);

    try {
      const newSchedule = await fetchNFLDataAsync(undefined);
      ReactDOM.unstable_batchedUpdates(() => {
        setSchedule(newSchedule);
        setIsLoading(false);
      });
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(schedule));
    } catch {
      setIsLoading(false);
    }
  }

  const resetSchedule = () => {
    localStorage.removeItem(LOCAL_STORAGE_KEY);
    localStorage.removeItem(USER_DATA_LOCAL_STORAGE_KEY);

    setUserLocalData({
      removedGames: {
        date: '',
        games: {}
      }
    });

    setResetCounter(resetCounter + 1);

    console.log('>>> reseting');
    // console.log(schedule);
    // setSchedule({ games: [], displayDate: ''});
    // console.log(schedule);
    // upDateSchedule();
  }

  useEffect(() => {
    upDateSchedule();
  }, [resetCounter]);

  useVisibilityHandlers(upDateSchedule);

  // const [onVisibleCtr] = useVisibilityHandlers(() => undefined);

  // useEffect(() => {
  //   if (onVisibleCtr) upDateSchedule();
  // }, [upDateSchedule, onVisibleCtr]);

  // const [scheduleFromNetwork, isLoading, resetSchedule] = useFetchData(LOCAL_STORAGE_KEY);
  // const schedule = carryOverHiddenGames(scheduleFromNetwork, { displayDate: '', games: []});

  const removeGame = (id: number) => {
    const newUserLocalData = { ... userLocalData};

    // current removedGames points to older date
    if (newUserLocalData.removedGames.date !== schedule.displayDate) {
      newUserLocalData.removedGames.date = schedule.displayDate;
      newUserLocalData.removedGames.games = {};
    }

    newUserLocalData.removedGames.games[id] = true;

    setUserLocalData(newUserLocalData);
    localStorage.setItem(USER_DATA_LOCAL_STORAGE_KEY, JSON.stringify(newUserLocalData));
  }

  ///

  schedule.games.map(game => {
      game.hidden = false;
  });

  if (schedule.displayDate === userLocalData.removedGames.date) {
    schedule.games.map(game => {
      if(userLocalData.removedGames.games[game.id]) {
        console.log('hidden')
        game.hidden = true;}
    });
  }


  console.log('rendering...');
  // console.log(schedule);
  console.log(userLocalData);
  return (
    <Card
      title={
        <span>
          <span className="font-weight-bold">NFL</span>
          <span className="pl-2 font-weight-light font-italic text-lowercase text-muted">{schedule.displayDate}</span>
          <Button
            outline
            size="sm"
            className="float-right"
            onClick={resetSchedule}
            disabled={isLoading}
            style={{ width: '50px' }}
          >
            {isLoading ? <Spinner size="sm" color="primary" type="grow" /> : 'Reset'}
          </Button>
        </span>
      }
    >
      {isLoading && <Progress animated style={{ height: '5px' }} color="info" value={100} />}

      <GameTable games={schedule.games} removeGame={removeGame} />
    </Card>
  );
}
